* {
  -webkit-overflow-scrolling: touch;
}

.loader {
  width: 75px;
  position: absolute;
  bottom: 80px;
  left: 16px;
  z-index: 1;
}

.app {
  min-height: 100%;
  width: 100%;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.chat-body {
  -ms-flex-item-align: center;
      align-self: center;
  width: 100%;
  height: 100%;
}

.small .emojione {
  width: 24px;
  height: 24px;
  margin: 0 2.5px;
  -webkit-transform: translateY(4px);
      -ms-transform: translateY(4px);
          transform: translateY(4px);
}

@media screen and (min-width: 768px) {
  .chat-body {
    width: 90%;
    height: 80%;
    border-radius: 3px;
    max-width: 750px;
  }

  header {
    border-radius: 3px 3px 0 0;
  }
}



